body {
  margin: 0;
  background: linear-gradient(to bottom, rgb(33, 34, 34), rgb(74, 85, 87), rgb(97, 122, 122), rgb(106, 144, 153));
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

.my-4 {
  margin-top: 5.5rem !important;
  margin-bottom: 1.5rem !important;
}

section {
  height: 100vh;
  /* Each section takes up the full viewport height */
  position: relative;
}

/* Add this to your CSS file */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  /* Make sure it stays on top */
}

.bg-transparent {
  --bs-bg-opacity: 1;
  background: linear-gradient(to top, rgb(33, 34, 34), rgb(74, 85, 87), rgb(97, 122, 122), rgb(106, 144, 153));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 70px;
  color: bisque;
}

h2 {
  font-size: 40px;
  color: bisque;
}

h3 {
  font-size: 35px;
  color: bisque;
}

#LC {
  width: 40px;
}

img {
  width: 70px;
  aspect-ratio: "3/2";
  object-fit: contain;
  mix-blend-mode: color-burn;
}

.btncss {
  font-size: 50px;
  width: 400px;
  border-radius: 50px;
  --bs-btn-bg: #e8dfdf;
}

iframe {
  width: 365px;
  height: 232px;
  border-radius: 10px;
  margin-top: 30px;
  margin-left: 50px;
}


button h3 {
  color: black;
  font-size: 30px;
}

#btn1 {
  border-radius: 70px;
  height: 50px;
  display: flex;
  align-items: center;
  margin-top: 30px;
  margin-left: 40%;
}

#person {
  width: 400px;
  height: 400px;
}

.btn1css {
  background-color: #323333;
}

.evsubul1 {
  text-align: justify;
  display: flex;
  gap: 30px;
}

.aboutme2 img {
  margin-top: 40px;
  width: 160px;
  mix-blend-mode: inherit;
}

@media screen and (max-width: 514px) {
  h1 {
    font-size: 30px;
    color: bisque;
  }

  h2 {
    font-size: 20px;
    color: bisque;
  }

  h3 {
    font-size: 15px;
    color: bisque;
  }

  #LC {
    width: 50px;
    height: 80px;
  }

  img {
    width: 30px;
  }

  .btncss {
    font-size: 20px;
    width: 200px;
    border-radius: 50px;
    --bs-btn-bg: #e8dfdf;
  }

  iframe {
    width: 380px;
    height: 300px;
  }

  #btn1 {
    border-radius: 50px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  button h3 {
    color: black;
    font-size: 20px;
  }

  #person {
    width: 400px;
    height: 400px;
    bottom: 70px;
  }

  .aboutme1 {
    flex: 0 0 auto;
    width: 100%;
    height: 340px;
    overflow-y: auto;
  }

  .aboutme2 {
    flex: 0 0 auto;
    width: 100%;
    height: 100%;
    bottom: 130px;
    position: relative;
  }

  .aboutme2 img {
    width: 100px;
    mix-blend-mode: inherit;
  }

  .evsub1 {
    flex: 0 0 auto;
    width: 100%;
    height: 340px;
    overflow-y: auto;
  }

  .evsub2 {
    flex: 0 0 auto;
    width: 90%;
    bottom: 110px;
    position: relative;
  }

  iframe {
    width: 320px;
    height: 230px;
  }

  .evsubul1 {
    text-align: justify;
    display: block;
  }
}